.rfi-form-wpr {
    position: relative;
    margin-top: -60px;
    width: 100%;
    @media screen and (min-width: $screen-sm-min) {
        margin-top: -90px;
    }
}

.rfi-form-wpr.active {
    z-index: 1040;
}

.rfi-form {
    background: $lt-dark-gray-1;
    padding: $grid-gutter-width;
    border-top: 5px solid $lt-red;
    position: relative;
    z-index: 2;
    width: 100%;
    .screen-reader-response {
        display: none;
    }
    h3 {
        position: relative;
        padding-left: 25px;
        .icon-rarrow {
            @include icon(arrow, 15px, 25.5px, $lt-red);
            display: block;
            width: 15px;
            height: 25px;
            position: absolute;
            left: 0;
            top: 50%;
            margin-top: -13px;
            outline: 1px solid $lt-red;
        }
    }
    &:after {
        content: '';
        display: block;
        position: absolute;
        @include icon(rfibg, 4px, 4px, transparent);
        background-repeat: repeat;
        background-size: 4px 4px;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
    .rfi-copy,
    .rfi {
        position: relative;
        z-index: 3;
    }
    form.sent {
        .rfi-copy,
        .rfi {
            display: none;
        }
        .wpcf7-response-output {
            display: block;
            text-align: center;
            color: #fff;
            padding: 20px;
            font-size: 1.3em;
            position: relative;
            z-index: 1003;
        }
    }
}

.rfi-form-shadow {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    &:before {
        position: absolute;
        width: 100px;
        min-height: 70%;
        content: '';
        display: block;
        left: 0px;
        background: transparent;
        box-shadow: -9px 7px 13px 8px rgba(0, 0, 0, 0.35);
        transform-origin: top left;
        transform: skewX(4deg);
        -webkit-transform-origin: top left;
        -webkit-transform: skewX(4deg);
        top: 10px;
    }
    &:after {
        position: absolute;
        width: 100px;
        min-height: 70%;
        content: '';
        display: block;
        right: 0px;
        background: transparent;
        box-shadow: 9px 7px 13px 8px rgba(0, 0, 0, 0.35);
        transform-origin: top left;
        transform: skewX(-4deg);
        -webkit-transform-origin: top left;
        -webkit-transform: skewX(-4deg);
        top: 10px;
    }
}

@media screen and (min-width: $screen-sm-min) {
    .two-column-rfi {
        .container {
            .row {
                >div:first-child {
                    float: right !important;
										padding-bottom: 100px;
                }
            }
        }
    }
}

.full-width,
.half-width {
    width: 100%;
    padding: 10px 0;
    position: relative;
		display: inline-block;
    p {
        margin: 0;
    }
}

.full-width {
    @include clearfix;
}

@media screen and (min-width: $screen-md-min) {
    .half-width {
        width: 48%;
        float: left;
        margin-left: 4%;
    }
    .half-width:nth-child(2n) {
        margin-left: 0;

        &:after {
            width: 1px;
            display: block;
            float: none;
            clear: both;
        }
    }
}


/*Form*/

label,
.label,
p.label {
    color: $lt-white;
    font-size: 0.9em;
    display: block;
    margin-bottom: 4px;
    &.inline {
        display: inline-block;
        margin-left: 10px;
        margin-top: 0;
    }
}

input[type="text"],
input[type="tel"],
input[type="email"],
input[type="number"],
input[type="tel"],
input,
select {
    height: 36px;
    font-size: 16px;
    border: 2px solid transparent;
    background: #ffffff;
    border-radius: 3px;
    display: block;
    width: 100%;
    padding-left: 4px;
    padding-right: 30px;
    position: relative;
    color: #000;
}

.select-input {
    position: relative;
    &:after {}
}

input[type="number"],
input[type="tel"] {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: #fff;
    &::-ms-expand {
        display: none;
    }
}

input[type="checkbox"],
input[type="radio"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 36px;
    height: 36px;
    display: inline-block;
    border: 2px solid transparent;
}

input[type="radio"] {
    border-radius: 50%;
    position: relative;
}

input[type="checkbox"]:checked {
    @include icon(chk, 20px, 17px, $lt-red);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 20px 17px;
    width: 36px;
    height: 36px;
}

input[type="radio"]:checked {
    position: relative;
    &:after {
        content: '';
        display: block;
        width: 20px;
        height: 20px;
        background: $lt-red;
        top: 6px;
        left: 6px;
        position: absolute;
        border-radius: 50%;
    }
}

#is_cell-error {
    position: absolute;
    bottom: 0;
    left: 0;
}

@media screen and (min-width: $screen-sm-min) {
    input[type="submit"],
    button[type="submit"],
    button#dummy-submit {
        display: inline-block;
        float: right;
        width: auto;
    }
    .one-column-rfi {
        input[type="submit"] {
            width: 100%;
            float: none;
        }
    }
}

.select-wpr {
    display: block;
    width: 100%;
    overflow: hidden;
    position: relative;
    &:after {
        content: '';
        display: block;
        @include icon(arrow, 11px, 19px, $lt-black);
        transform: rotate(90deg);
        -webkit-transform: rotate(90deg);
        position: absolute;
        top: 10px;
        right: 15px;
				pointer-events: none;
    }
}

.checkbox-wpr {
    position: relative;
    &.inline {
        display: inline-block;
        margin-right: 40px;
    }
    input[type="checkbox"] {}
    label {
        //display: block;
        //margin-left: 45px;
        //margin-top: -30px;
        &.long {
            margin-top: -40px;
        }
        span {
            font-size: 0.85em;
            line-height: 1.2em;
            display: block;
            margin-left: 50px;
            margin-top: -40px;
        }
    }
}

@media screen and (min-width: $screen-md-min) {
    .one-column-rfi {
        .rfi-form {
            display: table;
        }
        .rfi-copy {
            width: 30%;
            display: table-cell;
            vertical-align: middle;
        }
        .rfi {
            width: 68%;
            display: table-cell;
            vertical-align: middle;
            padding-left: 2%;
            .rfi-inputs {
                width: 60%;
                display: table-cell;
                vertical-align: middle;
            }
            .rfi-submit {
                width: 36%;
                padding-left: 4%;
                display: table-cell;
                vertical-align: middle;
            }
        }
    }
}

label.error {
    font-size: 0.85em;
    color: #ffc107;
    line-height: 1;
    margin: 0.4em 0 0 0;
}

.is_cell {
    @include clearfix;
    span.first {
        label {
            display: inline-block;
            padding-right: 1em;
        }
    }
    span.last {
        label {
            display: inline-block;
        }
    }
    span {
        label {
            span {
                display: inline-block;
                float: right;
                line-height: 2.5em;
                font-size: 0.9em;
                margin-left: 0.2em;
            }
        }
    }
    &:after {
        display: block;
        float: none;
        clear: both;
        width: 1px;
    }
}

select.error,
input.error {
    border: 2px solid #ff0000;
}

select.valid,
input.valid {
    border: 2px solid #00a651;
}

input[type='text'].error,
input[type='email'].error,
input[type='tel'].error,
select.error {
    @include iconbg(inValidIcon, #ff0000);
    background-size: 26px;
    background-repeat: no-repeat;
    background-position: right 8px;
}

input[type='text'].valid,
input[type='email'].valid,
input[type='tel'].valid,
select.valid {
    border: 2px solid #0dd20d;
    @include iconbg(validIcon, #0dd20d);
    background-size: 26px;
    background-repeat: no-repeat;
    background-position: right 8px;
}

select.error,
select.valid {
    padding-right: 60px;
    background-position: calc(100% - 28px) 8px;
}

@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
    input[type='radio'].error {
        outline: 2px solid #ff0000;
        border: 0;
    }
    input[type='radio'].valid {
        outline: 2px solid #0dd20d;
        border: 0;
    }
}

.modal-background {
    display: none;
    z-index: 1032;
    /* Hidden by default */
    position: fixed;
    /* Stay in place */
    //z-index: 1;
    /* Sit on top */
    left: 0;
    top: 0;
    width: 100%;
    /* Full width */
    height: 100%;
    /* Full height */
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4);
    /* Black w/ opacity */
}

.modal-background.active {
    display: block;
}