main {
    margin-top: 69px;
    @media screen and (min-width: $screen-sm-min) {
        margin-top: 93px;
    }
}

.section-wpr {
    padding: 30px 0;
    @media screen and (min-width: $screen-sm-min) {
        padding: 50px 0;
    }
}

.section-heading {
    margin: 0 0 1em 0;
    @include heading-deco(50px, 5px, $lt-red, 0.7em 0 0 0);
    text-align: left;
    text-transform: uppercase;
    &--center {
        margin: 0 0 1em 0;
        @include heading-deco(50px, 5px, $lt-red, 0.7em auto 0 auto);
        text-align: center;
        text-transform: uppercase;
    }
    &--center-left {
        margin: 0 0 1em 0;
        @include heading-deco(50px, 5px, $lt-red, 0.7em auto 0 auto);
        text-align: center;
        text-transform: uppercase;
        @media screen and (min-width: $screen-sm-min) {
            @include heading-deco(50px, 5px, $lt-red, 0.7em 0 0 0);
            text-align: left;
        }
    }
    &--left-center {
        margin: 0 0 1em 0;
        @include heading-deco(50px, 5px, $lt-red, 0.7em 0 0 0);
        text-transform: uppercase;
        @media screen and (min-width: $screen-sm-min) {
            text-align: center;
            @include heading-deco(50px, 5px, $lt-red, 0.7em auto 0 auto);
        }
    }
}

@media screen and (max-width: $screen-sm-max) {
    .add-divider {
        margin-bottom: $grid-gutter-width;
    }
}

.text {
    &--black {
        color: $lt-black;
    }
    &--red {
        color: $lt-red;
    }
    &--white {
        color: $lt-white;
    }
}

.button-link {
    padding: 5px 0;
    a {
        padding-left: 15px;
        padding-right: 46px;
        background: $lt-light-gray-3;
        color: $lt-black;
        font-weight: bold;
        text-decoration: none;
        display: block;
        position: relative;
        border-radius: 4px;
        width: 100%;
        height: 4em;
        display: flex;
        align-items: center;
        word-wrap: break-word;
        overflow: hidden;
        padding-top: 10px;
        padding-bottom: 10px;
        &:after {
            @include icon(arrow, 11px, 19px, $lt-red);
            width: 11px;
            height: 19px;
            display: block;
            content: '';
            position: absolute;
            top: 50%;
            margin-top: -9px;
            right: 15px;
        }
        &:hover {
            background: $lt-light-gray-1;
        }
    }
    &--disabled {
        margin: 10px 0 0 0;
        padding: 0 15px;
        background: $lt-light-gray-3;
        color: $lt-black;
        font-weight: bold;
        text-decoration: none;
        display: block;
        position: relative;
        border-radius: 4px;
        width: 100%;
        height: 4em;
        display: flex;
        align-items: center;
        word-wrap: break-word;
        overflow: hidden;
        padding-top: 10px;
        padding-bottom: 10px;
    }
}